export default class {
    constructor(cb) {
        this.thens = [];
        this.resolved = false;
        cb((value) => {
            this.resolved = true;
            for (let then of this.thens) {
                then(value);
            }
        }, (error) => {
            this.resolved = true;
            if (this.catch_) {
                this.catch_(error);
            }
        });
    }
    then(cb) {
        if (this.resolved) {
            return;
        }
        this.thens.push(cb);
        return this;
    }
    catch(cb) {
        if (this.resolved) {
            return;
        }
        this.catch_ = cb;
        return this;
    }
}
;
